function Footer(props) {
    return (
        <div className={`${props.fixed && 'fixed-bottom'} text-center py-2 w-100`} style={{backgroundColor: 'white', ...props.style, zIndex: 4}}>
            <footer><a href="https://saharzzz.github.io/saharhayon/" className={`${props.class} link-unstyled text-decoration-none`}>{`© ${new Date().getFullYear()} Copyright: Sahar Hayon`}</a></footer>
        </div>
    )
}

export default Footer;